import { Component, OnInit, Input } from '@angular/core';
import { Answer, Question, Doctor, Patient } from '@panadoc/data';
import * as moment from 'moment';
@Component({
  selector: 'panadoc-card-answer',
  templateUrl: './card-answer.component.html',
  styleUrls: ['./card-answer.component.scss']
})
export class CardAnswerComponent implements OnInit {
  @Input() answer: Answer;
  @Input() question: Question
  @Input() asker: Patient;
  @Input() answerer: Doctor;
  date: string;
  constructor() { }
  ngOnInit() {
    this.date = moment(this.answer.date)
      .locale('es')
      .fromNow();

  }
}
