import { Component, OnInit, EventEmitter, Output, Input, OnChanges, SimpleChanges, SimpleChange, OnDestroy } from '@angular/core';
import { Observable, of } from 'rxjs';
import { ThemeService } from '../../theme/theme.service';
import { Store, select } from '@ngrx/store';
import { PanaState } from '../../reducers';
import { nextForm, saveForm, FORM_STATE, startSaveForm, extraState, initForm, setProfileEditable, setProfileDisable } from '../../reducers/extra';
import { User, determineIfUserIsDoctor, mockDoctor, mockPatient } from '@panadoc/data';
import { login } from '../../auth/auth.actions';
import { map } from 'rxjs/operators';
// import { Extra}
@Component({
  selector: 'panadoc-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnChanges, OnInit, OnDestroy {
  @Output() sidenavToggle = new EventEmitter<void>();
  @Output() drawerToggle = new EventEmitter<void>();
  @Input() matDrawerShow;
  @Input() url = "";
  @Input() title: string = "Panadoc";
  formState: FORM_STATE = FORM_STATE.INIT;
  isFormDisabled: boolean = true;
  isDarkTheme: Observable<boolean>;
  forceHide = false;
  auth;
  showBackArrow = false;
  showFrontArrow = false;
  isDoctor;
  constructor(
    private themeService: ThemeService,
    private store: Store<PanaState>
  ) {
    this.store.pipe(select('extra')).subscribe((v: extraState) => {
      this.formState = v.formState;
      this.showBackArrow = v.formState === FORM_STATE.SECOND_PAGE;
      if (this.auth) {
        this.showFrontArrow = !(this.formState === FORM_STATE.SECOND_PAGE && this.isDoctor);
      }
    })
    // this.store.pipe(select('router')).pipe(map(v => v && v.state && (<string>v.state.url).replace('/', ''))).subscribe(url=>);

    this.store.pipe(select('auth')).subscribe((v: User) => {
      this.auth = v;
      this.isDoctor = determineIfUserIsDoctor(v);
      this.showFrontArrow = this.formState === FORM_STATE.INIT && this.isDoctor;

    })
  }
  toggleDarkTheme(checked: boolean) {
    this.themeService.setDarkTheme(checked);
  }
  goBack() {
    this.store.dispatch(initForm())
  }
  cta() {
    switch (this.formState) {
      case FORM_STATE.INIT:
        if (this.isDoctor) {
          this.store.dispatch(nextForm())
        } else {
          this.store.dispatch(startSaveForm())
        }
        return;
      case FORM_STATE.SECOND_PAGE:
        this.store.dispatch(nextForm())
        return;
    }
  }
  switchEditingState() {
    this.isFormDisabled = !this.isFormDisabled;
    this.store.dispatch(this.isFormDisabled ? setProfileEditable() : setProfileEditable());
  }
  handleToggle() {
    this.onToggleSidenav();
    this.onToggleDrawer();
  }
  ngOnInit() {
    this.isDarkTheme = this.themeService.isDarkTheme;
    this.setupTitle()
  }
  ngOnChanges(changes: SimpleChanges): void {
    const url: SimpleChange = changes.url;
    if (url && url.currentValue) {
      this.setupTitle(url.currentValue);
    }
  }
  setupTitle(_url = this.url) {
    if (!_url) return
    const params = _url.split("/").filter((v, i) => i % 2)
    const url = params[0];
    switch (url) {
      case 'perfil':
        this.title = 'Editar Perfil de Usuario'
        break
      case 'preguntas':
        this.title = 'Panadoc Preguntas'
        break
      case 'mensajeria':
        const subParam = params[1];
        if (subParam) {
          this.forceHide = true; return;
        };
      default:
        this.title = "Panadoc"
    }
    this.forceHide = false;
  }
  onToggleSidenav() {
    this.sidenavToggle.emit();
  }
  onToggleDrawer() {
    this.drawerToggle.emit();
  }
  ngOnDestroy(): void {
    this.store.dispatch(setProfileDisable());
  }
}
