import {
  ActionReducer,
  ActionReducerMap,
  createFeatureSelector,
  createSelector,
  MetaReducer
} from '@ngrx/store';
import {
  StoreRouterConnectingModule,
  routerReducer,
  RouterReducerState,
  RouterStateSerializer
} from "@ngrx/router-store";

import { environment } from '../../environments/environment';
import { authReducer } from '../auth/auth.reducer';
import { Params, RouterStateSnapshot } from "@angular/router";
import { localStorageSync } from 'ngrx-store-localstorage';

import { extraState} from './extra'
import { extraReducer } from './extra/extra.reducer';
import { Injectable } from "@angular/core";

import { User } from '@panadoc/data';
export interface PanaState {
  auth: User,
  router: RouterReducerState<RouterStateUrl>;
  extra:extraState,
}

export const reducers: ActionReducerMap<PanaState> = {
  auth: authReducer,
  router: routerReducer,
  extra:extraReducer,
};
export function localStorageSyncReducer(reducer: ActionReducer<any>): ActionReducer<any> {
  const config={
    keys: ['auth'],
    rehydrate:true,
    removeOnUndefined:true,
  }
  return localStorageSync(config)(reducer);
}
export interface RouterStateUrl {
  url: string;
  params: Params;
  queryParams: Params;
}

@Injectable()
export class CustomSerializer implements RouterStateSerializer<RouterStateUrl> {
  serialize(routerState: RouterStateSnapshot): RouterStateUrl {
    let route = routerState.root;

    while (route.firstChild) {
      route = route.firstChild;
    }

    const {
      url,
      root: { queryParams }
    } = routerState;
    const { params } = route;

    // Only return an object including the URL, params and query params
    // instead of the entire snapshot
    return { url, params, queryParams };
  }
}

export const metaReducers: MetaReducer<PanaState>[] = !environment.production
  ? [localStorageSyncReducer]
  : [localStorageSyncReducer];

export const selectAuthFeature = (state: PanaState) => state.auth;
export const selectReducerState = createFeatureSelector<
  RouterReducerState<RouterStateUrl>
>("router");
export const getRouterInfo = createSelector(
  selectReducerState,
  state => state.state
);

export const selectExtraFeature = (state) => state.extra;

export const loadingSelector = createSelector(
  selectExtraFeature,
  state => state.loading
);