import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Appointment, mockAppointment, IUser, mockPatient } from '@panadoc/data';
import * as moment from 'moment';

@Component({
  selector: 'ui-card-appointment',
  templateUrl: './card-appointment.component.html',
  styleUrls: ['./card-appointment.component.scss']
})
export class CardAppointmentComponent implements OnInit {
  @Input() appointment: Appointment;
  @Input() patient: IUser;
  @Output() rescheduled = new EventEmitter<Appointment>();
  @Output() canceled = new EventEmitter<Appointment>();
  day: string;
  startTime: string;
  finishTime: string;

  constructor() { }
  cancel(e) { this.canceled.emit(this.appointment) }
  reschedule(e) { this.rescheduled.emit(this.appointment) }

  getTime(date) {
    return moment(date)
      .locale('es')
      .format('hh:mm a');
  }
  ngOnInit() {
    this.startTime = this.getTime(this.appointment.initDate);
    this.finishTime = this.getTime(this.appointment.finishDate);
    this.day = moment(this.appointment.initDate)
      .locale('es')
      .format('dddd, D [de] MMMM');
  }
}
