import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Observable, of } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { User, mockPatient, mockDoctor, determineIfUserIsDoctor } from '@panadoc/data';
import { HttpService } from '../http-service-base.service';
import * as jwt_decode from 'jwt-decode';


@Injectable({
  providedIn: 'root'
})
export class AuthService {
  endpoint = '/auth';

  constructor(private http: HttpClient) {
  }

  login(data): Observable<User | boolean> {
    let headers = new HttpHeaders();
		headers.append('Content-Type','application/json');
    let body = {
      username: data.username,
      password: data.password
    }
    var address = environment.localServer + this.endpoint+'/';

    return this.http.post(address, body, {headers: headers})
      .pipe(
        //@ts-ignore
        map(({ user, token }) => ({ ...user, token, uid: user._id })),
        map(v => {
          console.log(v);
          const isDoctor = determineIfUserIsDoctor(v as User);
          return {
            ...isDoctor ? mockDoctor : mockPatient,
            ...v
          }
        }),
        catchError((val) => of(false)),
      )
  }

  decode(){
    try{
      var token = localStorage.getItem('token');
      return jwt_decode(token);
    }
    catch(Error){
        return null;
    }
  }

  storeData(storeData: any){
    localStorage.setItem('token', storeData.token);
    localStorage.setItem('loggedIn', storeData.auth);
  }

  isAuthenticated(){
    const loggedIn = localStorage.getItem('loggedIn');
    const isLogged = (loggedIn == 'true')
    return isLogged;
  }

  getToken(){
    const token = localStorage.getItem('token');
    return token;
  }

  signup(data): Observable<User | boolean> {
    return this.http.post(`${environment.localServer}/auth/signup`, data)
      .pipe(
        map(v => (v as User)),
        catchError((val) => of(false))
      );
  }
}
