interface IMenu {
  name: string;
  link: string;
  icon?: string;
  svgIcon?: string;
  isUser?: boolean;
  chip?: boolean;
  open?: boolean;
  sub?: IMenu[];
  isAdmin?: boolean;
}
class Menu implements IMenu {
  name: string;
  link: string;
  icon?: string;
  svgIcon?: string;
  isUser?: boolean;
  chip?: boolean;
  open?: boolean;
  sub?: IMenu[];
  isAdmin?: boolean;
  constructor(i: Menu) {
    this.name = i.name;
    this.link = i.link;
    this.icon = i.icon ? i.icon : 'input';
    this.svgIcon = i.svgIcon ? i.svgIcon : '';
    this.isUser = i.isUser ? i.isUser : true;
    this.chip = i.chip ? i.chip : false;
    this.open = i.open ? i.open : false;
    this.sub = i.sub ? i.sub : [];
    this.isAdmin = i.isAdmin ? i.isAdmin : false;
  }
}
