import * as moment from 'moment';
import { Component, OnInit, Input } from '@angular/core';
import { ChatUnit, User } from '@panadoc/data';

@Component({
  selector: 'chat-unit',
  templateUrl: './chat-unit.component.html',
  styleUrls: ['./chat-unit.styles.scss']
})
export class ChatUnitComponent implements OnInit {
  @Input() item: ChatUnit;
  @Input() user: User;
  @Input() author: User;
  @Input() date: Date;
  modDate: string
  isFromUser: boolean;
  constructor() { }

  ngOnInit() {
    this.isFromUser = this.user.uid === this.author.uid;
    this.modDate = moment(this.date).locale('es').fromNow(false)
  }

}
