import { Component, OnInit, Input, OnDestroy, OnChanges } from '@angular/core';
import {
  User,
  determineIfUserIsDoctor,
  SocketPackage as Pkg,
  ChatUnit,
  SocketEventType as Type,
  Chat,
} from '@panadoc/data';
import { ActivatedRoute, Router } from '@angular/router';
import { select, Store } from '@ngrx/store';
import { PanaState } from '../../reducers';
import { ChatSocketService } from './chat-socket.service';
import { CommunicationsService } from '../communications.service';
import { Observable, merge, Subscription, of } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  templateUrl: './chat.container.component.html',
  styleUrls: ['./chat.container.component.scss']
})
export class ChatComponent implements OnInit, OnDestroy {
  isDoctor: boolean;
  user: User;
  chat: any;
  chatId: string;
  receiver: User;
  subs = new Subscription();
  typing: boolean = false;
  receiverConnected = false;
  loading = true;
  constructor(
    private store: Store<PanaState>,
    private route: ActivatedRoute,
    private http: CommunicationsService,
    private router: Router,
    public socket: ChatSocketService,
  ) {
    this.subs.add(
      this.route.paramMap.subscribe(params => {
        if (params.has('chatId')) {
          this.chatId = params.get('chatId');
        }
        else {
          this.goBack();
        }
      })
    )
  }

  ngOnInit() {
    this.socket.connect();
    this.subs.add(this.socket.listen().subscribe(v => this.listen(v)))
    this.subs.add(this.store.pipe(select('auth')).subscribe((user: User) => {
      this.isDoctor = determineIfUserIsDoctor(user);
      this.user = user;
      this.emit(new Pkg(Type.CHAT_ADD_USER, {
        user,
        chatUid: (this.chat && this.chat.uid) || undefined,
      }))
    }));
  }

  communicate(v) {
    switch (v) {
      case 'CALL':
        this.router.navigateByUrl('/audio/' + this.chatId)
        break
      case 'VIDEO':
        this.router.navigateByUrl('/video/' + this.chatId);
        break;
      default:
        break
    }
  }
  goBack() {
    this.router.navigateByUrl('/mensajeria')
  }
  appointDate() {
    this.router.navigateByUrl('/agenda/nueva-cita/' + (this.receiver).uid)
  }
  updateLog(pkg) {
    if ([
      pkg.payload.receiverId,
      pkg.paylod.senderId
    ]
      .some(id => id === this.user.uid))
      this.chat.log.push(pkg.payload)
  }
  emit(pkg: Pkg) {
    switch (pkg.type) {
      case Type.CHAT_NEW_MESSAGE:
        this.updateLog(pkg);
        break;
      default:
        break;
    }
    this.socket.emit(pkg);
  }

  listen(pkg: Pkg) {
    switch (pkg.type) {
      case Type.CHAT_LOGIN:
        this.chat = pkg.payload;
        this.receiver = (<User[]>pkg.payload.members).find(({ uid }) => uid !== this.user.uid);
        this.loading = false
        break;
      case Type.N_USERS:
        this.receiverConnected = (pkg.payload >= 2)
        break;
      case Type.CHAT_USER_JOINED:
        this.receiverConnected = true;
        break;
      case Type.CHAT_USER_LEFT:
        this.receiverConnected = false;
        break;
      case Type.CHAT_NEW_MESSAGE:
        this.updateLog(pkg);
        break;
      case Type.CHAT_NO_TYPING:
        this.typing = false;
        break;
      case Type.CHAT_TYPING:
        this.typing = true;
        break;
      default:
        break;
    }
    console.log(pkg);
  }
  ngOnDestroy(): void {
    this.socket.disconnect();
    this.subs.unsubscribe();
  }
}
