import { Component, OnInit, Input } from '@angular/core';
import { Doctor, mockDoctor, Review, mockReview } from '@panadoc/data';
import * as moment from 'moment';

@Component({
  selector: 'panadoc-card-review',
  templateUrl: './card-review.component.html',
  styleUrls: ['./card-review.component.scss']
})
export class CardReviewComponent implements OnInit {
  @Input() doctor;
  @Input() review;
  date: string;
  constructor() {}

  ngOnInit() {
    this.date = moment(this.review.date)
      .add(4, 'days')
      .add(20, 'minutes')
      .locale('es')
      .toNow();
  }
}
