import { Injectable } from '@angular/core';
import { of, Observable } from 'rxjs';
import { mockDoctor, mockPatient, mockChatPreview, Chat, User, ChatItem, ChatUnit, ChatPreview, mockLog, mockChat } from '@panadoc/data';
import { switchMap, switchMapTo, map, catchError } from 'rxjs/operators';
import { HttpService } from '../http-service-base.service';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';

const baseUrl = environment.httpServer + '/communications'

@Injectable({
  providedIn: 'root'
})
export class CommunicationsService extends HttpService {
  myId: string;
  constructor(private http: HttpClient) {
    super();
  }
  fetchChatList(myId: string): Observable<Array<ChatPreview>> {
    this.myId = myId;
    //@ts-ignore
    return this.http.get<Array<ChatPreview>>(`${baseUrl}/chatsERRROR/${myId}`, this.headers)
      .pipe(
        catchError(e => {
          console.error(e);
          const me = mockPatient.uid === this.myId ? mockPatient : mockDoctor;
          const other = mockPatient.uid === this.myId ? mockDoctor : mockPatient;
          return of(mockChatPreview(me, other) as any);
        })
      );

  }
  getUser(id): Observable<User> {
    if (mockDoctor.uid === id) { return of(mockDoctor) }
    if (mockPatient.uid === id) { return of(mockPatient) }
  }
}
