import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  OnChanges,
  Inject
} from '@angular/core';
import { MediaChange, MediaObserver } from '@angular/flex-layout';
import { Subscription, Observable, of } from 'rxjs';
import { tap } from 'rxjs/operators';
import { DomSanitizer } from '@angular/platform-browser';
import { MatIconRegistry } from '@angular/material';
import { ThemeService } from './theme/theme.service';
import { User, UserType, determineIfUserIsDoctor } from '@panadoc/data';
import { PanaState } from './reducers';
import { Store, select } from '@ngrx/store';

@Component({
  selector: 'panadoc-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, OnChanges {
  isDarkTheme: boolean;
  auth$: Observable<User>;
  @Input() isVisible = true; //
  visibility = 'shown';
  sideNavOpened = true; // 1
  matDrawerOpened = false; // 0
  matDrawerShow = true; // 1
  sideNavMode = 'side';
  shouldHide = false;
  url: string;
  $subs = new Subscription();
  constructor(
    private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer,
    private themeService: ThemeService,
    private mediaObserver: MediaObserver,
    private store: Store<PanaState>,
  ) { }

  ngOnInit() {
    this.addIcons();
    this.auth$ = this.store.pipe(select('auth'));
    this.store.pipe(select('router')).subscribe((state) => {
      if (!state) return
      const { state: { url } } = state;
      const whitelist = ['/auth/login', '/auth/signup', '/login', '/logout', '/signup']
      this.shouldHide = whitelist.some(v => v === url)
      this.url = url;
    });

    this.auth$.subscribe((auth: User) => {
      const isDoctor = determineIfUserIsDoctor(auth);
      this.isDarkTheme = isDoctor;
    });
    this.$subs = this.mediaObserver.asObservable().subscribe(change => {
      this.toggleView();
    });
  }
  ngOnChanges() {
    this.visibility = this.isVisible ? 'shown' : 'hidden';
  }
  addIcons() {
    this.addSvg('facebook');
    this.addSvg('instagram');
    this.addSvg('twitter');
  }
  private addSvg(nombre: string) {
    this.matIconRegistry.addSvgIcon(
      nombre,
      this.domSanitizer.bypassSecurityTrustResourceUrl(
        '..assets/data-icons/' + nombre + '.svg'
      )
    );
  }
  toggleView() {
    if (this.mediaObserver.isActive('gt-md')) {
      this.sideNavMode = 'side';
      this.sideNavOpened = true; // 1
      this.matDrawerOpened = false; // 0
      this.matDrawerShow = true; // 1
    } else if (this.mediaObserver.isActive('gt-xs')) {
      this.sideNavMode = 'side';
      this.sideNavOpened = false; // 0
      this.matDrawerOpened = true; // 1
      this.matDrawerShow = true; // 1
    } else if (this.mediaObserver.isActive('lt-sm')) {
      this.sideNavMode = 'over';
      this.sideNavOpened = false; // 0
      this.matDrawerOpened = false; // 0
      this.matDrawerShow = false; // 0
    }
  }
}
