import { Injectable } from '@angular/core';
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  Router
} from '@angular/router';
import { Store, select } from '@ngrx/store';
import { PanaState } from '../reducers';
import { AuthService } from './auth.service';
import { User, UserType } from '@panadoc/data';

@Injectable()
export class AuthGuard implements CanActivate {
  isAuth: boolean = false;

  constructor(
    private router: Router,
    private auth: AuthService,
    private store: Store<PanaState>) {

 /*    this.store.pipe(select('auth')).subscribe(auth => {
      this.isAuth = (auth && auth.uid && [UserType.DOCTOR, UserType.PATIENT].some(v => v === auth.type))
    }); */
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    this.isAuth = this.auth.isAuthenticated();
    if (this.isAuth) {
      return true;
    } else {
      this.router.navigate(['/login']);
    }
  }
}
