import { IUser, User } from './user.model';

interface ChatUnit {
  uid?: string,
  senderId: string | User,
  receiverId?: string | User,
  message: string,
  date: Date,
}

enum ChatEventType {
  CALL_MADE = 'call_made',
  CALL_MISSED_INCOMING = 'call_missed',
  CALL_RECEIVED = 'call_received',
  CALL_MISSED_OUTGOING = 'call_missed_outgoing',
  VIDEOCALL = 'video_call',
  VIDEOCALL_MISSED = 'missed_video_call',
  //Material Icon Name
}


interface ChatEvent {
  eventType: ChatEventType;
  date: Date;
}
type ChatItem = ChatEvent | ChatUnit;
function isChatUnit(toBeDetermined) {
  if ((toBeDetermined as ChatUnit).senderId) {
    return true
  }
  return false
}
function determineIfIsChatUnit(toBeDetermined: ChatItem): toBeDetermined is ChatUnit {
  return isChatUnit(toBeDetermined);
}

interface Chat {
  uid: string;
  log: Array<ChatItem>
  members: Array<string | User>;
}
interface ChatPreview {
  lastMessage: ChatUnit,
  contact: User
}
export {
  Chat,
  ChatPreview,
  ChatUnit,
  ChatEvent,
  ChatEventType,
  ChatItem,
  determineIfIsChatUnit,
  isChatUnit,
}