import { Component, OnInit, EventEmitter, Output, Inject, Input } from '@angular/core';
import {
  MatBottomSheet,
  MatBottomSheetRef,
  MAT_BOTTOM_SHEET_DATA
} from '@angular/material/bottom-sheet';

import { FormControl } from '@angular/forms';
import { mockDoctor, Doctor, Review } from '@panadoc/data';
@Component({
  selector: 'ui-review-btn',
  template:
    ' <button mat-button (click)="openBottomSheet()">Valorar</button>',
  styleUrls: ['./new-review.component.scss']
})
export class NewReviewButtonComponent {
  constructor(private _bottomSheet: MatBottomSheet) { }
  @Input() doctor: Doctor;
  @Output() newReview = new EventEmitter<any>();

  openBottomSheet(): void {
    const bottomSheetRef = this._bottomSheet.open(
      NewReviewSheetContentComponent,
      {
        data: { doctor: this.doctor }
      }
    );
    bottomSheetRef.afterDismissed().subscribe(
      results => {
        results && this.newReview.emit(results)
      })
  }
}
@Component({
  templateUrl: './new-review.component.html',
  styleUrls: ['./new-review.component.scss']
})
export class NewReviewSheetContentComponent implements OnInit {
  text = new FormControl('');
  doctor: Doctor;
  rating: number;
  ngOnInit() { }

  constructor(
    @Inject(MAT_BOTTOM_SHEET_DATA) public data: any,
    private _bottomSheetRef: MatBottomSheetRef<NewReviewSheetContentComponent>
  ) {
    this.doctor = data.doctor;
  }

  onRatingChange({ rating }) {
    this.rating = rating;
  }

  close(event) {
    this._bottomSheetRef.dismiss();
    event.preventDefault();
  }

  save(event) {
    const newReview: Review = {
      date: new Date(),
      text: this.text.value,
      rating: this.rating,
      doctorId: this.doctor.uid,
      patientId: '',
    };
    this._bottomSheetRef.dismiss(newReview);
    event.preventDefault();
  }
}
