export interface ISocketPackage {
  type: SocketEventType;
  payload: any;
}
export class SocketPackage implements ISocketPackage {
  type: SocketEventType;
  payload: any;
  constructor(
    i: ISocketPackage | SocketEventType,
    payload: any = undefined,
  ) {
    if ((i as ISocketPackage).type) {
      const _i: ISocketPackage = (i as ISocketPackage);
      this.type = _i.type;
      this.payload = _i.payload;
    } else {

      this.type = <SocketEventType>i;
      this.payload = payload;
    }
  }
}
