//@ts-nocheck
import { Injectable } from '@angular/core';
import { environment } from 'apps/panadoc/src/environments/environment';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { User } from '@panadoc/data';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
const SERVERS: any = {
  iceServers: [
    { urls: "stun:stun.services.mozilla.com" },
    { urls: "stun:stun.l.google.com:19302" },
    { 'urls': 'turn:numb.viagenie.ca', 'credential': 'sonvoito', 'username': 'p4rkogno@gmail.com' }
  ],
};
const DEFAULT_CONSTRAINTS = {
  optional: []
};
declare let RTCPeerConnection: any;
const s4 = () => Math.floor((1 + Math.random()) * 0x10000).toString(16).substring(1);
const calcUID = () => `${s4()}${s4()}-${s4()}-${s4()}-${s4()}-${s4()}${s4()}${s4()}`

@Injectable({
  providedIn: 'root'
})
export class CallService {
  pc: any;
  channel: AngularFireList<{ sender: string, message: string }>
  duration:'';
  chatId:string;
  database: any;
  senderId: string;
  constructor(private afDb: AngularFireDatabase) { }
  setupWebRtc(onTrackHandler,chatId) {
    this.senderId = calcUID();
    this.chatId=chatId;
    var channelName = "/webrtc";
    this.channel = this.afDb.list(channelName);
    this.database = this.afDb.database.ref(channelName);

    this.database.on("child_added", this.readMessage.bind(this));
    this.pc = new RTCPeerConnection(SERVERS, DEFAULT_CONSTRAINTS);
    this.pc.onicecandidate = event =>
      event.candidate
        ? this.sendMessage(
          this.senderId,
          JSON.stringify({ ice: event.candidate,chatId })
        )
        : console.log("Sent All Ice");

    this.pc.ontrack = onTrackHandler; // use ontrack

  }

  sendMessage(senderId, data) {
    var msg = this.channel.push({
      sender: senderId,
      message: data
    });
    msg.remove();
  }

  readMessage(data) {
    if (!data) return;
    var msg = JSON.parse(data.val().message);
    const isSameMsg=msg.chatId===this.chatId
    var sender = data.val().sender;
    if (sender != this.senderId&&isSameMsg) {
      if (msg.ice != undefined&&isSameMsg)

        this.pc.addIceCandidate(new RTCIceCandidate(msg.ice));
      else if (msg.sdp && msg.sdp.type == "offer")
        this.pc
          .setRemoteDescription(new RTCSessionDescription(msg.sdp))
          .then(() => this.pc.createAnswer())
          .then(answer => this.pc.setLocalDescription(answer))
          .then(() =>
            this.sendMessage(
              this.senderId,
              JSON.stringify({ sdp: this.pc.localDescription,chatId:this.chatId })
            )
          );
      else if (msg.sdp.type == "answer")
        this.pc.setRemoteDescription(new RTCSessionDescription(msg.sdp));
    }
  }
  showRemote() {
    this.pc
      .createOffer()
      .then(offer => this.pc.setLocalDescription(offer))
      .then(() =>
        this.sendMessage(
          this.senderId,
          JSON.stringify({ sdp: this.pc.localDescription,chatId:this.chatId })
        )
      );
  }
  hangUp(){}
  getUserMedia(audio: boolean, video: boolean): any {
    return new Observable(observer => {
      navigator.mediaDevices
        .getUserMedia({ audio, video })
        .then((stream: MediaStream) => {
          observer.next(stream);
        })
        .catch((error: MediaStreamError) => {
          observer.error(error);
        });
    })
      .pipe(tap(
        stream => {
          console.log(stream);
          this.pc.addStream(stream);
        }
      ))
  }
}
