import { Component, OnInit, Input, EventEmitter, Output, OnChanges, SimpleChanges, SimpleChange } from '@angular/core';
import { Patient, Doctor, User, determineIfUserIsDoctor, ChatUnit } from '@panadoc/data';
import { of } from 'rxjs';

@Component({
  selector: 'ui-chat-list-item',
  templateUrl: './chat-list-item.component.html',
  styleUrls: ['./chat-list.component.scss']
})
export class ChatListItemComponent implements OnInit {
  @Input() contact: User;
  @Input() lastMessage: ChatUnit;
  @Output() select = new EventEmitter<boolean>();

  public userIcon: string
  constructor() { }

  ngOnInit(): void {
    this.userIcon = determineIfUserIsDoctor(this.contact) ? 'assets/png-icons/icon-doctor.png' : 'assets/png-icons/icon-user.png'
  }
  handleSelect() {
    this.select.emit(true);
  }
}

@Component({
  selector: 'ui-chat-list',
  templateUrl: './chat-list.component.html',
  styleUrls: ['./chat-list.component.scss']
})
export class ChatListComponent implements OnChanges, OnInit {
  @Input() chats: Array<{
    contact: Patient | Doctor,
    lastMessage: ChatUnit,
    available: boolean,
  }> = [];
  loading: boolean = false;
  @Output() select = new EventEmitter<number>();
  constructor() { }

  ngOnInit() {

  }
  ngOnChanges(changes: SimpleChanges) {
    const chats: SimpleChange = changes.chats;
    this.loading = chats.currentValue.length === 0;
  }
  selectChat(index: number) {
    this.select.emit(index)
  }

}
