import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Question, Patient, Answer } from '@panadoc/data';

@Component({
  selector: 'panadoc-card-question',
  templateUrl: './card-question.component.html',
  styleUrls: ['./card-question.component.scss']
})
export class CardQuestionComponent implements OnInit {
  constructor() { }
  @Input() question: Question;
  @Input() asker: Patient;
  @Input() doctorId: string;
  @Output() answerEmitter = new EventEmitter<Answer>();
  text;
  ngOnInit() { }
  answer() {
    const answer: Answer = { doctorId: this.doctorId, date: new Date(), text: this.text }
    this.answerEmitter.emit(answer);
  }
}
