import * as moment from 'moment';
import { Component, OnInit, Input } from '@angular/core';
import { ChatEvent, ChatEventType } from '@panadoc/data';

@Component({
  selector: 'chat-event',
  template: `
    <mat-list-item
      class="chatEvent"
      fxLayoutAlign="space-around end"      
      
    >
        <mat-icon>{{ item.eventType }}</mat-icon>
        <h4 >{{ message }} {{ date }}</h4>
    </mat-list-item>
  `,
  styleUrls: ['./chat-unit.styles.scss']
})
export class ChatEventComponent implements OnInit {
  @Input() item: ChatEvent;
  @Input() date: string;
  message: string;

  ngOnInit() {
    this.message = this.getChatEventMessage(this.item.eventType);
    this.date = moment(this.date).locale('es').fromNow();
  }
  getChatEventMessage(e: ChatEventType) {
    switch (e) {
      case ChatEventType.CALL_MADE:
        return 'Se ha realizado una llamada';
      case ChatEventType.CALL_MISSED_INCOMING:
        return 'Se ha recibido una llamada entrante';
      case ChatEventType.CALL_MISSED_OUTGOING:
        return 'Se perdió una llamada saliente';
      case ChatEventType.CALL_RECEIVED:
        return 'Se ha recibido una llamada';
      case ChatEventType.VIDEOCALL:
        return 'Se realizó una videollamada';
      case ChatEventType.VIDEOCALL_MISSED:
        return 'Se perdió una videollamada'
    }
  }
  constructor() { }


}
