import { Document } from 'mongoose';
import { Review } from './review.model';
import { Appointment } from './appointment.model';
export enum CommType {
  CHAT = 1,
  CALL = 2,
  VIDEO = 4,
}
export function getCommunications(val: number): CommType[] {
  switch (val) {
    case 0:
      return [];
    case 1:
      return [CommType.CHAT];
    case 2:
      return [CommType.CALL];
    case 3:
      return [CommType.CHAT, CommType.CALL];
    case 4:
      return [CommType.VIDEO];
    case 5:
      return [CommType.VIDEO, CommType.CHAT];
    case 6:
      return [CommType.VIDEO, CommType.CALL];
    case 7:
      return [CommType.VIDEO, CommType.CALL, CommType.CHAT];
    default:
      return [];
  }
}
export enum UserType {
  PATIENT = 'patient',
  DOCTOR = 'doctor',
  PHARMA = 'pharma',
  SALESREP = 'business',
}
export interface IUser {// extends Document {
  uid?: string;
  name: string;
  avatarSrc: string;
  type: UserType;
  token?: string;
  phone?: string,
  mhsId?: string[],
  username: string;
  mail?: string;
  password?: string;
  appointments: Appointment[] | string[];
}

export interface Patient extends IUser {
}

export interface Doctor extends IUser {
  speciality: string;
  addr?: string;
  reviews?: Review[] | string[];
  experience?: string;
  likes?: number;
  likePerc?: number;
  summary?: string;
  mhsId?: string[];
}
export type User = Doctor | Patient;

export function determineIfUserIsDoctor(user: User): user is Doctor {
  return (user && user.type === UserType.DOCTOR);
}

export function determineIfUserIsPatient(user: User): user is Patient {
  return (user && user.type === UserType.PATIENT);
}