import { Component, OnInit } from '@angular/core';
import { CommunicationsService } from '../communications.service';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { PanaState, loadingSelector } from '../../reducers';
import { of } from 'rxjs';
import { ChatPreview, User } from '@panadoc/data';

@Component({
  templateUrl: './chat-list.container.component.html',
  styleUrls: ['./chat-list.container.component.scss']
})
export class ChatListComponent implements OnInit {
  chats: ChatPreview[] = [];
  user: User;
  $loading = of(false)
  constructor(
    private http: CommunicationsService,
    private router: Router,
    private store: Store<PanaState>
  ) {
    this.$loading = this.store.select(loadingSelector)
    this.store.select('auth').subscribe(user =>
      this.user = user
    )
  }

  ngOnInit() {
    this.http.fetchChatList(this.user.uid)
      .subscribe(v => {
        this.chats = v
      })
  }
  select(chatId: number) {
    this.router.navigateByUrl('/mensajeria/chat/' + chatId)
  }

}
