import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { mockDoctor, Doctor } from '@panadoc/data';

@Component({
  selector: 'ui-card-doctor',
  templateUrl: './card-doctor.component.html',
  styleUrls: ['./card-doctor.component.scss']
})
export class CardDoctorComponent implements OnInit {
  @Input() doctor: Doctor;
  @Output() appointment = new EventEmitter<Doctor>();
  @Output() newReview = new EventEmitter<Doctor>();
  @Output() redirect = new EventEmitter<string>();

  constructor() { }
  ngOnInit() { }
  newAppointment() { this.appointment.emit(this.doctor) }
  review(newReview) {
    this.newReview.emit(newReview)
  }
  redirectTo() {
    this.redirect.emit(this.doctor.uid);
  }
}
