import * as Extra from './index';
import { createReducer, on, Action, createSelector } from '@ngrx/store';


const reducer = createReducer(
  Extra.initialState,
  on(Extra.nextForm, (state: Extra.extraState) => ({
    ...state,
    formState: Extra.FORM_STATE.SECOND_PAGE,
  })),
  on(Extra.startSaveForm, (state: Extra.extraState) => ({
    ...state,
    formState: Extra.FORM_STATE.START_SAVE,
  })),
  on(Extra.saveForm, (state: Extra.extraState, { formData }) => ({
    ...state,
    formState: Extra.FORM_STATE.SAVED,
    formData,
  })),
  on(Extra.initForm, (state: Extra.extraState) => ({
    ...state,
    formState: Extra.FORM_STATE.INIT,
  })),
  on(Extra.initForm, (state: Extra.extraState) => ({
    ...state,
    formState: Extra.FORM_STATE.INIT,
  })),
  on(Extra.startLoading, (state: Extra.extraState) => ({
    ...state,
    loading: true,
  })),
  on(Extra.stopLoading, (state: Extra.extraState) => ({
    ...state,
    loading: false,
  })),
  on(Extra.setProfileDisable, (state) => ({ ...state, isFormDisabled: true })),
  on(Extra.setProfileEditable, (state) => ({ ...state, isFormDisabled: false }))
);

export function extraReducer(state: Extra.extraState | undefined, action: Action) {
  return reducer(state, action);
}