import { createReducer, on } from '@ngrx/store';
import { login, logout, signup } from './auth.actions';
import { User, mockPatient, mockDoctor } from '@panadoc/data';


const _authReducer = createReducer(undefined,
  //@ts-ignore
  on(login, (state, { payload }) => (payload)),
  on(signup, (state, { payload }) => (payload)),
  on(logout, () => Object()),
);

export function authReducer(state, action) {
  return _authReducer(state, action);
}