import { Injectable } from '@angular/core';
import { Socket } from 'ngx-socket-io';
import {
  SocketEventType as Type,
  SocketPackage as Pkg,
} from '@panadoc/data';
import { mapTo, tap, map } from 'rxjs/operators';
import { Observable, Subscription, of, merge, from } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ChatSocketService {
  constructor(private socket: Socket) {

  }
  emit(pkg: Pkg, cb: Function = (v) => { debugger }) {
    switch (pkg.type) {
      // NOTE: If socketio callback needed implement here.  
      default:
        this.socket.emit(pkg.type, pkg.payload, cb);
        break;
    }
  }
  connect() {
    this.socket.connect();
  }
  listen(): Observable<Pkg> {
    return merge(
      this.socket.fromEvent(Type.CHAT_TYPING),
      this.socket.fromEvent(Type.CHAT_NO_TYPING),
      this.socket.fromEvent(Type.CHAT_USER_JOINED),
      this.socket.fromEvent(Type.CHAT_USER_LEFT),
      this.socket.fromEvent(Type.CHAT_NEW_MESSAGE),
      this.socket.fromEvent(Type.CHAT_LOGIN),
      this.socket.fromEvent(Type.N_USERS),
    ).pipe(tap(v => { debugger })) as Observable<Pkg>;
  }
  disconnect() {
    this.socket.disconnect();
  }

}
