import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CommType, IUser, mockPatient } from '@panadoc/data';
@Component({
  selector: 'panadoc-button-comm',
  template: `
  <ng-container *ngIf="fab; else normal">
    <button 
      mat-fab-button
      aria-label="Escribir al Doctor" 
      (click)="cta()">
      <mat-icon>
        {{ icon }}
      </mat-icon>
    </button>
    </ng-container>
    <ng-template #normal>
      <button 
        mat-icon-button
        aria-label="Escribir al Doctor" 
        (click)="cta()">
        <mat-icon>
          {{ icon }}
        </mat-icon>
      </button>
    </ng-template>
  
  `,
  styleUrls: ['./button-comms-row.component.css']
})
export class ButtonCommsComponent implements OnInit {
  @Input() comm: CommType;
  @Input() fab: boolean;
  @Output() click = new EventEmitter<CommType>();
  icon: string;
  cta() {
    this.click.emit(this.comm);
  }
  ngOnInit() {
    switch (this.comm) {
      case CommType.CHAT:
        this.icon = 'chat_bubble';
        break;
      case CommType.CALL:
        this.icon = 'call';
        break;
      case CommType.VIDEO:
        this.icon = 'video_call';
        break;
    }
  }
}
@Component({
  selector: 'ui-button-comm-row',
  template: `
    <div
      class="communications-buttons"
      fxLayoutAlign="space-between center"
      fxLayoutGap="1rem"
    >
      <span *ngFor="let comm of comms">
        <panadoc-button-comm 
        [comm]="comm" 
        [fab]="fab"
        (click)="onCommunicate($event)"
        > </panadoc-button-comm>
      </span>
    </div>
  `,
  styleUrls: ['./button-comms-row.component.css']
})
export class ButtonCommsRowComponent implements OnInit {
  @Input() user: IUser;
  comms = [CommType.CALL, CommType.CHAT, CommType.VIDEO];
  @Input() fab: boolean;
  @Output() click = new EventEmitter<string>();
  onCommunicate(n: number) {
    const value = CommType[n];
    if (!Number.isNaN(n) && value) {
      this.click.emit(value)
    }
  }

  ngOnInit() { }

}
