import { Component, OnInit } from '@angular/core';
import { PanaState } from '../../reducers';
import { Store, select } from '@ngrx/store';
import { UserType, User, mockDoctor, Doctor, determineIfUserIsDoctor } from '@panadoc/data';
import { Observable, of } from 'rxjs';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { switchMap } from 'rxjs/operators';
import { MguideService } from '../../mguide/mguide.service';

@Component({
  selector: 'panadoc-public-profile',
  templateUrl: './public-profile.component.html',
  styleUrls: ['./public-profile.component.scss']
})
export class PublicProfileComponent implements OnInit {
  user: User;
  profile: Doctor;
  constructor(
    private store: Store<PanaState>,
    private route: ActivatedRoute,
    private router: Router,
    private http: MguideService) {
    this.store.pipe(select('auth')).subscribe((auth) => {
      this.user = auth;
      this.http.token = auth.token;
    });
  }

  ngOnInit() {
    this.route.paramMap.subscribe((params) => {
      if (params.has('doctorId')) {
        //TODO: DOCTOR PROFILE SHOW;
        const doctorId = params.get('doctorId');
        this.http.getDoctor(doctorId).subscribe((v: Doctor) => {
          this.profile = v
        });
      } else if (determineIfUserIsDoctor(this.user)) {
        this.profile = (this.user)
      } else {
        this.router.navigateByUrl('/');
      }
    });
  }

}
