import { Component, Input, Output, EventEmitter, OnInit, Type } from '@angular/core';
import { MaskedInputDirective } from 'angular2-text-mask';
import { UserType, determineIfUserIsDoctor, User } from '@panadoc/data';

@Component({
  selector: 'app-file-upload',
  templateUrl: './file-upload.component.html',
  styleUrls: ['./file-upload.component.scss']
})
export class FileUploadComponent implements OnInit {
  @Input() tipo: File;
  @Output() filesEvent = new EventEmitter<File[]>();
  @Output() eventIsDoctor = new EventEmitter<boolean>();
  @Input() userDefined: boolean | UserType = false;
  isHovering: boolean;
  files: File[] = [];

  userIsDoctor: boolean = false;
  doctorSrc: string = `assets/svg-icons/doctor-color.svg`;
  patientSrc: string = `assets/svg-icons/patient-color.svg`;
  doctorIcon = this.setIconType(this.userIsDoctor)
  patientIcon = this.setIconType(!this.userIsDoctor);
  doctorTooltip = this.setTextType(this.userIsDoctor)
  patientTooltip = this.setTextType(!this.userIsDoctor);

  switchIcon() {
    this.userIsDoctor = !this.userIsDoctor;
    this.eventIsDoctor.emit(this.userIsDoctor);
    this.doctorIcon = this.setIconType(this.userIsDoctor)
    this.patientIcon = this.setIconType(!this.userIsDoctor);
    this.doctorTooltip = this.setTextType(this.userIsDoctor)
    this.patientTooltip = this.setTextType(!this.userIsDoctor);
    this.doctorSrc = `assets/svg-icons/doctor-${this.userIsDoctor ? 'color' : 'mono'}.svg`
    this.patientSrc = `assets/svg-icons/patient-${!this.userIsDoctor ? 'color' : 'mono'}.svg`
  };

  constructor() {

  }
  ngOnInit() {
    if (this.userDefined) {
      this.userIsDoctor = this.userDefined === UserType.DOCTOR;
    } else {
      this.switchIcon()
    }
  }

  addFiles(event: FileList) {
    this.files = [event[0]];
    this.done();
  }
  done() {
    this.filesEvent.emit(this.files);
  }
  toggleHover(event: boolean) {
    this.isHovering = event;
  }
  openInput() {
    document.getElementById('file-input').click();
  }
  cta(doctorIconChosen: boolean) {
    if (doctorIconChosen) {
      if (this.userIsDoctor) { this.openInput() }
      else { this.switchIcon() }
    } else {
      if (this.userIsDoctor) { this.switchIcon() }
      else { this.openInput() }
    }
  }
  setTextType(isChosen) {
    return (isChosen) ? 'Seleccione Foto' : 'Cambiar Tipo de Usuario'
  }
  setIconType(isChosen) {
    return isChosen ? 'attach_file' : 'sync_alt';
  }

}
