import { IMenu, AdminMenu, BasicMenu, UserMenu } from './menu.elements';
import { User, UserType } from '@panadoc/data';

// const findSub = (m: IMenu) => m.name === 'Musica';
export function mapMenuByUserType(user: User): IMenu[] {
  switch (user.type) {
    case UserType.DOCTOR:
      return AdminMenu.concat(BasicMenu)
    case UserType.PATIENT:
    default:
      return UserMenu.concat(BasicMenu)
  }
}
export function mapMenu(user: { name: string }, subs: IMenu[], menu: IMenu[]) {
  if (user.name !== '') {
    return menu.reduce((arr: IMenu[], m: IMenu): IMenu[] => {
      //   m.sub = (findSub(m)) ? subs : [];
      return arr.concat(m);
    }, []);
  } else {
    return menu;
  }
}
