import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ErrorComponent } from './components/error/error.component';
import { AuthGuard } from './auth/auth.guard';
import { WelcomeComponent } from './components/welcome/welcome.component';
import { MyProfileComponent } from './components/my-profile/my-profile.component';
import { PublicProfileComponent } from './components/public-profile/public-profile.component';
import { NgxMaterialTimepickerModule } from 'ngx-material-timepicker';

const routes: Routes = [
  {
    path: 'login',
    redirectTo: 'auth/login'
  },
  {
    path: '',
    redirectTo: 'agenda',
    pathMatch: 'full',
    canActivate: [AuthGuard]
  },
  {
    path: 'logout',
    redirectTo: 'auth/logout'
  },
  {
    path: 'signup',
    redirectTo: 'auth/signup'
  },
  {
    path: 'doctor/:doctorId',
    component: PublicProfileComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'mi-perfil',
    component: PublicProfileComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'perfil',
    component: MyProfileComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'agenda',
    loadChildren: './mguide/mguide.module#MguideModule',
    canActivate: [AuthGuard]
  },
  {
    path: 'preguntas',
    loadChildren: './ask-adoc/ask-adoc.module#AskADocModule', canActivate: [AuthGuard]

  },
  {
    path: 'mensajeria',
    loadChildren: './communications/communications.module#CommunicationsModule', canActivate: [AuthGuard]

  },
  {
    path: 'auth',
    loadChildren: './auth/auth.module#AuthModule'
  },
  {
    path: '**',
    component: ErrorComponent
  }
];
@NgModule({
  imports: [RouterModule.forRoot(routes), NgxMaterialTimepickerModule.setLocale('es-VE')],
  exports: [RouterModule],
  providers: [AuthGuard]
})
export class AppRoutingModule { }
