import { Component, OnInit, ViewChildren, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { User, UserType as type, } from '@panadoc/data';
import { Observable, of } from 'rxjs';
import { Store, select } from '@ngrx/store';
import { PanaState, loadingSelector } from '../../reducers';
import { MatStepper } from '@angular/material/stepper';
import { MatButton } from '@angular/material';
import * as Extra from '../../reducers/extra'
@Component({
  selector: 'panadoc-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.scss']
})
export class MyProfileComponent implements OnInit {
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  user: User;
  userType: type;
  UserType = type;
  disabled = false;
  formState: Extra.FORM_STATE = Extra.FORM_STATE.INIT;
  stepperIndex: number = 0;
  @ViewChild('stepper', { static: true }) public myStepper: MatStepper;
  $loading = of(false);
  constructor(
    private store: Store<PanaState>,
    private _formBuilder: FormBuilder
  ) {
    this.$loading = this.store.select(loadingSelector)

    this.store.pipe(select('auth')).subscribe((user) => {
      this.user = user;
      this.userType = user.type;
    });
    this.store.pipe(select('extra')).subscribe(({ formState, isFormDisabled }) => {
      this.formState = formState;
      this.disabled = isFormDisabled;
      switch (formState) {
        case Extra.FORM_STATE.START_SAVE:
          this.store.dispatch(Extra.saveForm({ formData: 'modifiedData' }))
          return;
        case Extra.FORM_STATE.INIT:
          this.stepperIndex = 0;
          return;
        case Extra.FORM_STATE.SECOND_PAGE:
          this.stepperIndex = 1;
          return;
        case Extra.FORM_STATE.SAVED:
          this.stepperIndex = 0;
          return;
      }
    })
  }
  changed({ selectedIndex }) {
    if (selectedIndex === 1) { this.store.dispatch(Extra.nextForm()) }
    if (selectedIndex === 0) { this.store.dispatch(Extra.initForm()) }

  }
  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ['', Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
  }

}
