import {
  Component,
  OnInit,
  EventEmitter,
  Output,
  Input,
  OnDestroy
} from '@angular/core';
import { Observable, of, Subscription } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { BasicMenu, AnonMenu, IMenu, Menu, AdminMenu } from './menu.elements';
import { mapMenuByUserType } from './menu.mapper';
import { PanaState } from '../../reducers';
import { Store, select } from '@ngrx/store';
import { User } from '@panadoc/data';

@Component({
  selector: 'panadoc-sidenav-list',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.scss']
})
export class SidenavListComponent implements OnInit, OnDestroy {
  $user: Observable<User>;
  $menus: Observable<IMenu[]>
  @Input() iconOnly;
  @Output() closeSidenav = new EventEmitter<void>();
  @Input() userSubs: IMenu[];
  anonMenu = BasicMenu;
  private subscription = new Subscription();

  constructor(private store: Store<PanaState>) { }

  ngOnInit() {
    this.$user = this.store.pipe(select('auth'));
    this.$menus = this.$user.pipe(
      map(iUser => mapMenuByUserType(iUser))
    );
  }
  onClose() {
    this.closeSidenav.emit();
  }
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
  onLogout() {
    this.onClose();
    //TODO: Logout;
  }
}
