import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Subscription, Observable, of } from 'rxjs';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { AuthService } from '../auth.service';
import { PanaState, loadingSelector } from '../../reducers';
import { Store } from '@ngrx/store';
import { startLoading, stopLoading } from '../../reducers/extra';
import { login } from '../auth.actions';
import { User, UserType } from '@panadoc/data';
import { map, switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit, OnDestroy {
  $loading: Observable<boolean> = of(false);
  $subs = new Subscription();
  isDoctor = false;
  avatarSrc: File;
  public numberMask: Array<string | RegExp>;
  public dateMask: Array<string | RegExp>;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private http: AuthService,
    private store: Store<PanaState>
  ) {
    this.$loading = this.store.select(loadingSelector)

    this.numberMask = [
      '+',
      /[1-9]/,
      /\d/,
      ' ',
      '(',
      /[1-9]/,
      /\d/,
      /\d/,
      ')',
      ' ',
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      '-',
      /\d/,
      /\d/,
      /\d/
    ];
    this.dateMask = [/\d/, /\d/, '/', /\d/, /\d/, '/', /\d/, /\d/, /\d/, /\d/];
  }
  setUser(isDoctor) {
    this.isDoctor = isDoctor;
  }
  ngOnInit() {
    this.$subs.add(
      this.route.paramMap.subscribe((p: ParamMap) => {
        this.isDoctor = true;
        console.log('Cuenta de Doctor');
      })
    );
  }
  getAvatarFile(files: File[]) {
    this.avatarSrc = files.pop();
  }
  onSubmit(form: NgForm) {
    this.store.dispatch(startLoading());
    const {
      name,
      username,
      email: mail,
      phone,
      password,
      speciality,
      addrInput,
      summary,
      experience,
    } = form.value;
    let payload:any = {
      name,
      username,
      mail,
      phone,
      password,
      type: UserType.PATIENT,
      avatarSrc: this.avatarSrc,
    };
    if(this.isDoctor)payload={
      ...payload,
      type:UserType.DOCTOR,
      addr:addrInput,
      speciality,
      summary,
      experience
    }
    this.http.signup(payload)
      .pipe(
        switchMap(() => this.http.login({ username, password }))
      )
      .subscribe(data => {
        this.store.dispatch(stopLoading());
        if (data) {
          this.store.dispatch(login({ payload: <User>data }))
          this.router.navigateByUrl('/agenda')
        } else {
        }
      });
  }
  login() {
    this.router.navigateByUrl('/mguide');
  }
  ngOnDestroy() {
    this.$subs.unsubscribe();
  }
}
