import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { of, Subscription, Observable } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { CallService } from './call-webrtc.service';
@Component({
  templateUrl: './call.component.html',
  styleUrls: ['./call.component.scss']
})
export class CallComponent implements OnInit, OnDestroy {

  $stream: Subscription = new Subscription();
  $loading: Observable<boolean> = of(false);
  IS_VIDEO_CALL=true;
  isMutedAudio: boolean = false;
  isMutedMic: boolean = false;
  chatId;
  callActive: boolean = false;
  hasVideo: boolean = true;
  supportVideoSwitch = true;
  @ViewChild("me", { static: false }) me: any;
  @ViewChild("remote", { static: false }) remote: any;

  constructor(public call: CallService,route: ActivatedRoute){
    const constraints = navigator.mediaDevices.getSupportedConstraints();
    if (constraints['facingMode'] === true) {
      this.supportVideoSwitch = false;
    } else {
      this.supportVideoSwitch = true;
    }
    route.paramMap.subscribe(v => {
      if (v.has('videoId')) {
        this.chatId = v.get('videoId');
        this.IS_VIDEO_CALL = true;
      }
      if (v.has('audioId')) {
        this.chatId = v.get('audioId');
        this.IS_VIDEO_CALL = false;
      }
    })
  }

  ngOnInit() {
    const onTrackHandler = event => { this.remote.nativeElement.srcObject = event.streams[0] }
    this.call.setupWebRtc(onTrackHandler,this.chatId);
    this.$stream = this.call.getUserMedia(!this.isMutedMic, this.hasVideo)
    .subscribe(stream => {
      (this.me.nativeElement.srcObject = stream);
    })
  }


  showMe() {
   
  }
  hangUp() { }

  switchVideoSrc() {

  }
  switchMicState() { }
  switchAudioState() { }
  ngOnDestroy() {
    this.$stream.unsubscribe();
  }
}