const mockChat = (me: User, other: User): Chat => {
  return {
    members: [me, other],
    uid: '1234123213',
    log: mockLog.map((l: ChatUnit) => {
      const mine = l.senderId === me.uid;
      return {
        ...l,
        receiverId: mine ? other : me,
        senderId: mine ? me : other,
      }
    })
  }
}
const mockChatPreview = (me: User, other: User): ChatPreview[] => {
  return [...mockLog
    .filter(v => isChatUnit(v))
    .filter((c: ChatUnit) => c.senderId !== me.uid)
    .map((v: ChatUnit) => ({
      contact: other,
      lastMessage: v,
    }))]
}
