import { Component, OnInit, Input, EventEmitter, Output, OnDestroy, HostListener, OnChanges, SimpleChanges, SimpleChange, ViewChild, ElementRef, AfterViewChecked } from '@angular/core';
import {
  Chat,
  User,
  isChatUnit as _isChatUnit,
  determineIfUserIsDoctor,
  SocketEventType as Type,
  ISocketPackage as IPkg,
  SocketPackage as Pkg,
  ChatUnit,
} from '@panadoc/data';
import { fromEvent, timer, Observable, of, Subject } from 'rxjs';
import { mapTo, takeUntil, repeat, merge, distinctUntilChanged, tap, debounceTime } from 'rxjs/operators';
const TYPING_WAITING_TIME = 1000;
@Component({
  selector: 'ui-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit, OnDestroy, OnChanges, AfterViewChecked {
  text: string;
  isDoctor: boolean;
  @Input() typing: boolean;
  @Input() user: User;
  @Input() receiver: User;
  @Input() conversation: Chat;
  @Output() socketEvent = new EventEmitter<IPkg>();
  @Input() isChatUnit = _isChatUnit;
  @ViewChild('conversationInput', { static: true }) input: ElementRef;

  typingStream = new Subject();
  $sub = this.typingStream
    .pipe(
      debounceTime(TYPING_WAITING_TIME / 2),
      tap(() => this.socketEvent.emit(new Pkg(Type.CHAT_TYPING))),
      debounceTime(TYPING_WAITING_TIME * 2),
    )
    .subscribe(_ => this.socketEvent.emit(new Pkg(Type.CHAT_NO_TYPING)))
  constructor() {

  }
  ngAfterViewChecked() {
    this.scrollIntoView();

  }
  ngOnChanges(changes: SimpleChanges): void {
    const chat: SimpleChange = changes.conversation;
    if (chat.firstChange) {
    }
    this.scrollIntoView();
    console.log(chat.currentValue);
  }
  ngOnInit() {
    this.isDoctor = determineIfUserIsDoctor(this.user);
    this.scrollIntoView();
  }
  scrollIntoView() {
    this.input.nativeElement.scrollIntoView({ behavior: "smooth", block: "center", inline: "nearest" });

  }
  _sendText() {
    const newMsg: ChatUnit = {
      date: new Date(),
      message: this.text,
      senderId: this.user.uid,
      receiverId: this.receiver.uid,
    }
    this.socketEvent.emit(new Pkg(Type.CHAT_NEW_MESSAGE, newMsg));
    this.text = "";
  }
  @HostListener('document:keypress', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    this.typingStream.next();
    if (event.key === 'Enter') {
      event.preventDefault();
      this._sendText();
    }
  }

  getAuthor(item): User {
    const { senderId: id } = item;
    return id === this.user.uid ? this.user : this.receiver
  }
  addFiles(event: FileList) {
    const file = ([event[0]]).pop();
    this.socketEvent.emit(new Pkg(Type.CHAT_SEND_FILE))
  }
  openInput() {
    document.getElementById('file-input').click();
  }
  ngOnDestroy(): void {
    this.typingStream.complete();
  }
}

