import { props, createAction } from '@ngrx/store';

export enum FORM_STATE {
  INIT = 'INIT',
  START_SAVE = 'START_SAVE',
  SECOND_PAGE = 'SECOND_PAGE',
  SAVED = 'SAVED',
  START_LOADING = 'Started Loading',
  STOP_LOADING = 'Stop Loading',
  SET_PROFILE_EDITABLE = 'User editable',
  SET_PROFILE_DISABLE = 'User not editable',
}


export const nextForm = createAction(FORM_STATE.SECOND_PAGE);
export const saveForm = createAction(FORM_STATE.SAVED, props<{ formData: any }>());
export const startSaveForm = createAction(FORM_STATE.START_SAVE);
export const initForm = createAction(FORM_STATE.INIT);
export const startLoading = createAction(FORM_STATE.START_LOADING);
export const stopLoading = createAction(FORM_STATE.STOP_LOADING);
export const setProfileEditable = createAction(FORM_STATE.SET_PROFILE_EDITABLE);
export const setProfileDisable = createAction(FORM_STATE.SET_PROFILE_DISABLE);

export interface extraState {
  formData: any,
  formState: FORM_STATE,
  isFormDisabled: boolean,
  loading: boolean,
};

export const initialState: extraState = {
  formData: {},
  isFormDisabled: true,
  formState: FORM_STATE.INIT,
  loading: false,
};
