import { Injectable } from '@angular/core';
import { of, Observable } from 'rxjs';
import { mockDoctor, Doctor, Appointment } from '@panadoc/data';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment.prod';
import { map, tap, catchError } from 'rxjs/operators';
import { HttpService } from '../http-service-base.service';

const baseUrl = environment.httpServer + '/mguide'

@Injectable({
  providedIn: 'root'
})
export class MguideService extends HttpService {

  constructor(private http: HttpClient) {
    super()
  }
  getSymptoms() {
    console.log('get symptoms')

    return this.http.get(baseUrl + '/symptoms', this.headers)
      .pipe(catchError(e => {
        console.error(e);
        return of(['Fiebre'])
      }));
  }
  postSymptoms(newSymptoms: string[]): Observable<boolean> {
    console.log('post doctors', newSymptoms)

    return this.http.post(baseUrl + '/symptoms', newSymptoms, this.headers)
      .pipe(
        map(v => !!v),
        catchError(e => {
          console.error(e);
          return of(true);
        }));
  }
  getDoctors(params = undefined) {
    console.log('get doctors', params)
    if (params) {
      //TODO: Specify Type of Doctor;
    }
    return this.http.get(baseUrl + '/doctors', this.headers)
      .pipe(catchError(e => {
        console.error(e);
        return of([mockDoctor, mockDoctor, mockDoctor])
      }));;
  }
  getDoctor(doctorId) {
    console.log('get doctor', doctorId)
    return this.http.get(baseUrl + '/doctor/' + doctorId, this.headers)
      .pipe(catchError(e => {
        console.error(e);
        return of(mockDoctor);
      }));
  }
  deleteAppointment(appointmentId) {
    console.log('delete appointment', appointmentId)
    return this.http.delete(baseUrl + '/appointment/' + appointmentId, this.headers)
      .pipe(
        map(v => !!v),
        catchError(e => {
          console.error(e);
          return of(true);
        }));
  }
  getAppointment() { }
  postAppointment(appointment: any): Observable<boolean> {
    console.log('post appointment', appointment)

    return this.http.post(baseUrl + '/appointment/', appointment, this.headers)
      .pipe(
        map(v => !!v),
        catchError(v => of(true))
      )
  }
  putAppointment(appointment: any): Observable<boolean> {
    console.log('put appointment', appointment)
    const appointmentId = appointment.uid;
    return this.http.put(baseUrl + '/appointment/' + appointmentId, appointment, this.headers)
      .pipe(
        map(v => !!v),
        catchError(v => of(true as any))
      )
  }
  postReview(review): Observable<boolean> {
    console.log('review', review);
    return this.http.post(baseUrl + '/review/', review, this.headers)
      .pipe(
        map(v => !!v),
        catchError(v => of(true as any))
      )
  }
}
